import React from 'react';
import Container from '../components/Container';
import Accordion from '../components/Accordion';
import Cena from '../components/Cena';

const Cennik = () => {
    return (
        <Container title='Cennik'>
            <Accordion>
                <div label='Mycie powierzchni'>
                    <Cena
                        title='Mycie powierzchni paskiej'
                        cenaod='5'
                        cenado='7'
                        za='m'
                    >
                        Posadzka przemysowa, gara, kostka brukowa
                        (uzupenienie szczelin piaskiem 1 z / m)
                    </Cena>
                    <Cena
                        title='Mycie elewacji '
                        cenaod='6'
                        cenado='10'
                        za='m'
                    >
                        Cena uzaleniona od rodzaju tynku, wysokoci obiektu
                        oraz dostpu.
                        <br /> W przypadku zielonych nalotw (glonw) doliczamy
                        2 z / m naley dzie wczesniej spryska powierzchni
                        specjalistycznym rodkiem.
                    </Cena>
                    <Cena title='Mycie dachw' cenaod='5' cenado='9' za='m'>
                        W zalenoci od dostpu doliczamy koszt podnonika
                        koszowego.
                    </Cena>
                    <Cena
                        title='Impregnacja powierzchni'
                        cenaod='5'
                        za='m'
                    ></Cena>
                </div>
                <div label='Zakadanie ogrodw i trawnikw'>
                    <Cena
                        title='Usuga glebogryzark separacyjn'
                        cenaod='1,50'
                        cenado='2,50'
                        za='m'
                    >
                        W zalenoci od powierzchni i przeszkd na ogrodzie .
                    </Cena>
                    <Cena title='Rozkadanie siatki na krety' cena='2' za='m'>
                        W zalenoci od powierzchni i przeszkd na ogrodzie .
                    </Cena>
                    <Cena title='Wysiew trawy' cena='2' za='m'>
                        Cena nie zawiera materiaw
                    </Cena>
                    <Cena title='Niwelacja terenu i przygotowanie pod wysiew'>
                        Wycena indywidualna
                    </Cena>
                </div>
                <div label='Pielgnacja ogrodw i trawnikw'>
                    <Cena title='Koszenie ogrodw' cenaod='15' za='ar'>
                        Cena zalena od powierzchni oraz rodzaju koszenia (z
                        koszem lub bez)
                    </Cena>
                    <Cena title='Wertykulacja' cenaod='0,65' za='m'>
                        Za dodatkow opat istnieje moliwo zabrania trawy
                    </Cena>
                    <Cena title='Nawoenie' cenaod='0,50' za='m'></Cena>
                    <Cena title='Oprysk' cenaod='0,50' za='m'></Cena>
                    <Cena title='Zbieranie lici' cenaod='1,50' za='m'></Cena>
                    <Cena
                        title='Przycinanie ywopotu'
                        cenaod='4'
                        cenado='7'
                        za='m'
                    ></Cena>
                </div>
                <div label='Wycinka drzew'>
                    <Cena title='Wycena indywidualna' />
                </div>
                <div label='Prace alpinistyczne'>
                    <Cena title='Wycena indywidualna' />
                </div>
            </Accordion>
        </Container>
    );
};

export default Cennik;



